import { act } from 'react-dom/test-utils';
import * as types from './actionType';

const initialState = {
    products: [],
    product: [],
    productsInCart: [],
}

const productReducers = (state = initialState, action) => {
    switch (action.type) {
        case types.GET_PRODUCTS:
            return {
                ...state,
                products: action.payload,
            };


        case types.GET_SINGLE_PRODUCT:
            return {
                ...state,
                product: action.payload,
            };


        case types.ADD_PRODUCT_IN_CART:
            let upadatedProductsInCart;

            const existingProductsInCartIndex = state.productsInCart.findIndex(
                (product) => { return product.id === action.payload.id });

            const existingProductsInCart = state.productsInCart[existingProductsInCartIndex];

            if (existingProductsInCart >= 0) {
                const addProduct = {
                    ...existingProductsInCart,
                    totalProduct: existingProductsInCart.totalProduct + action.payload.totalProduct
                }
                upadatedProductsInCart = [...state.productsInCart, action.payload];
                upadatedProductsInCart[existingProductsInCart] = addProduct;

            }
            else {
                upadatedProductsInCart = [...state.productsInCart, action.payload];
            }
            return {
                ...state,
                productsInCart: upadatedProductsInCart,
            }

        case types.INCREASE_PRODUCT:
            let IncreasedProductsInCart;
            
            const IncreaseProductsInCartIndex = state.productsInCart.findIndex(
                (product) => { return product.id === action.payload });

            const IncreaseProductsInCart = state.productsInCart[IncreaseProductsInCartIndex];
            IncreasedProductsInCart = [...state.productsInCart];

            const AddItem = {   
                ...IncreaseProductsInCart,
                totalProduct : IncreaseProductsInCart.totalProduct + 1,
            }


            IncreasedProductsInCart[IncreaseProductsInCartIndex] = AddItem;
            return {
                ...state,
                productsInCart: IncreasedProductsInCart,
            }
        
        case types.DECREASE_PRODUCT: 
        let DecreasedProductsInCart;
            
        const DecreaseProductsInCartIndex = state.productsInCart.findIndex(
            (product) => { return product.id === action.payload });

        const DecreaseProductsInCart = state.productsInCart[DecreaseProductsInCartIndex];
        DecreasedProductsInCart = [...state.productsInCart];

        const RemoveItem = {   
            ...DecreaseProductsInCart,
            totalProduct : DecreaseProductsInCart.totalProduct - 1,
        }


        DecreasedProductsInCart[DecreaseProductsInCartIndex] = RemoveItem;
        return {
            ...state,
            productsInCart: DecreasedProductsInCart,
        }

        default:
            return state;
    }
}

export default productReducers;