import React from "react";
import "./Header.scss";
import VeniaLogo from "../../assets/Venia_logo.png";
import SearchImage from "../../assets/search.png";
import User from "../../assets/user.png";
import { useNavigate } from 'react-router-dom';
import { useSelector } from 'react-redux';




const Header = () => {
    
    let navigate = useNavigate();


    const  {productsInCart}  = useSelector(state => state.data)
    console.log("cart data" , productsInCart);

    return (
        <React.Fragment>
            <section>
                <div className="navbar">

                    <div className="nav-header">
                        <div className="nav-logo">
                            <a href="#">
                                <img src={VeniaLogo} alt="Venia_Logo"></img>
                            </a>
                        </div>
                    </div>

                    <input type="checkbox" id="nav-check" />
                    <div className="nav-btn">
                        <label for="nav-check">
                            <span></span>
                            <span></span>
                            <span></span>
                        </label>
                    </div>

                    <div className="nav-links">
                        <div className="center_nav">
                            <a href="#">Women</a>
                            <a href="#">Men</a>
                            <a href="#">Smart Gear</a>
                            <a href="#">Accessories</a>
                        </div>
                    </div>

                    <div className="nav-links Right_Nav_section">
                        <div className="Search_Part">
                            <img src={SearchImage} alt="Search_Icon"></img>
                            <a href="">Search</a>
                        </div>

                        <div className="User_Part">
                            <img src={User} alt="User_Icon"></img>
                            <a href="">Sign In</a>
                        </div>
                        
                        <div class="icon-wrapper">
                            <i class="fa fa-shopping-bag" aria-hidden="true"></i>
                            <span class="badge" onClick={() => navigate("/cart")}>{productsInCart.length}</span>
                        </div>
                    </div>

                </div>
                <hr></hr>
            </section>
        </React.Fragment>
    )
}

export default Header;