import axios from "axios";
import * as types from './actionType';

const getProducts = (products) => ({
    type: types.GET_PRODUCTS,
    payload: products,
})

const getProduct = (product) => ({
    type: types.GET_SINGLE_PRODUCT,
    payload: product,
})

const ProductInCart = (productCart) => ({
    type : types.ADD_PRODUCT_IN_CART,
    payload : productCart,
})

const IncreasedItems = (NumberOfItems) => ({
    type : types.INCREASE_PRODUCT,
    payload : NumberOfItems,
})

const DecreaseItems = (NumberOfItems) => ({
    type: types.DECREASE_PRODUCT,
    payload : NumberOfItems,
})

const RemoveProducts = (NumberOfItems) => ({
    type: types.REMOVE_PRODUCTS,
    payload : NumberOfItems
})

export const loadProducts = () => {
    return function(dispatch) {
        axios.get("https://fakestoreapi.com/products").then((res) => {
            console.log("Data", res);
            dispatch(getProducts(res.data));
        }).
        catch((error) => console.log(error));
    };
};

export const getSingleProduct = (id) => {
    return function(dispatch) {
        axios.get(`https://fakestoreapi.com/products/${id}`).then((res) => {
            dispatch(getProduct(res.data));
        })
        .catch((error) => console.log(error));
    };
};

export const AddProductInCart = (product) => {
    return function(dispatch) {
        dispatch(ProductInCart(product))
    };
};

export const IncreaseItemOfProduct = (product) => {
    return function(dispatch) {
        dispatch(IncreasedItems(product))
    };
};

export const DecreaseItemOfProduct = (product) => {
    return function(dispatch) {
        dispatch(DecreaseItems(product))
    }
}

export const RemoveProductFromCart = (product) => {
    return function(dispatch) {
        dispatch(RemoveProducts(product))
    }
}