import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { loadProducts } from '../../redux/action';
import { useNavigate } from "react-router-dom";

const ProductListing = () => {

    let dispatch = useDispatch();
    let navigate = useNavigate();
    const productStore = useSelector(state => state.data);

    const [firstPage, setFirstPage] = useState(0);
    const [secondPage, setSecondPage] = useState(12);

    const [showFilterBox, setShowFilterBox] = useState(false);
    const [showSortBox, setShowSortBox] = useState(false);


    useEffect(() => {
        dispatch(loadProducts());
    }, []);

    const onChangeFilterProducts = (event) => {

        let filterValue;
        if (event.target.checked) {
            filterValue = event.target.value;
            console.log("filt", filterValue);
            const filteredData = productStore.products.filter((data) => {

                return data.category === filterValue;
            });
            [...productStore.products] = filteredData;

        }
    }

    const onChangeSortProducts = (event) => {

        if (event.target.value === 'price_asc') {
            const filteredProducts = productStore.products.sort((product1, product2) => (product1.price > product2.price) ? 1 : -1);
            [...productStore.products] = filteredProducts;
            return;
        }

        if (event.target.value === 'price_dsc') {
            const filteredProducts = productStore.products.sort((product1, product2) => (product1.price < product2.price) ? 1 : -1);
            [...productStore.products] = filteredProducts;
            return;
        }
    }

    const paginationClickHandler = (event, id) => {

        event.preventDefault();
        if (id === 1) {
            setFirstPage(0);
            setSecondPage(12);
        }

        if (id === 2) {
            setFirstPage(12);
            setSecondPage(24);
        }
    }

    const showFilteredBox = () => {
        setShowFilterBox(false);
    }

    const hideFilteredBox = () => {
        setShowFilterBox(true);
    }

    const showSortredBox = () => {
        setShowSortBox(false);
    }

    const hideSortedBox = () => {
        setShowSortBox(true);
    }

    return (
        <React.Fragment>

            <section className="Wrapper_content">

                <div className="Wrapper_content_left">
                    <div className="category_heading">
                        <p>Clothing / Women's / Outwear</p>
                    </div>
                    <div className="filter_results">
                        <div onClick={() => showFilteredBox()}>Filter Result</div>
                        <div onClick={() => showSortredBox()}>Sort Products</div>
                    </div>
                    {!showFilterBox &&
                        <div className="filter_box">
                            <div className="cross_and_filter">
                                <h5>Filters</h5>
                                <div className="cross" onClick={() => hideFilteredBox()}>X</div>
                            </div>
                            <div className="horizontal_Line"></div>
                            <div className="Filters">
                                <input type='checkbox' name="men's clothing" value="men's clothing" onChange={onChangeFilterProducts}></input>
                                <p>Men's Clothing</p>
                            </div>
                            <div className="Filters">
                                <input type='checkbox' name="jewelery" value="jewelery" onChange={onChangeFilterProducts}></input>
                                <p>Jewellery</p>
                            </div>
                            <div className="Filters">
                                <input type='checkbox' name="electronics" value="electronics" onChange={onChangeFilterProducts}></input>
                                <p>Electronics</p>
                            </div>
                            <div className="Filters">
                                <input type='checkbox' name="women's clothing" value="women's clothing" onChange={onChangeFilterProducts}></input>
                                <p>Women's Clothing</p>
                            </div>
                        </div>
                    }
                </div>
                <div className="Wrapper_content_right">
                    <div className="Product_Sorting">
                        <h6>{productStore.products.length} Results</h6>
                        {!showSortBox &&
                           
                            <div className="sort_box">
                            <div className="cross" onClick={() => hideSortedBox()}>X</div>
                                <select name="cars" id="cars" onChange={onChangeSortProducts}>
                                    <option value="price_asc">Price Ascending</option>
                                    <option value="price_dsc">Price Descending</option>
                                </select>
                            </div>
                        }
                    </div>
                    <div className="Product_Listing">
                        {
                            productStore.products.filter((item, index) => index >= firstPage && index < secondPage).map((product, index) => {
                                return (
                                    <div className="Product_Data" key={index} onClick={() => navigate(`/singleproduct/${product.id}`)}>
                                        <img src={product.image} alt="Product_image"></img>
                                        <h5>{product.title}</h5>
                                        <p>${product.price}</p>
                                        <i class="fa fa-heart-o" aria-hidden="true"></i>
                                    </div>
                                )
                            })
                        }
                    </div>




                    <div class="pagination">
                        <a id='1' onClick={(event) => paginationClickHandler(event, 1)}>1</a>
                        <a id='2' onClick={(event) => paginationClickHandler(event, 2)}>2</a>
                        <a id='2' onClick={(event) => paginationClickHandler(event, 2)}>&gt;</a>
                    </div>

                </div>
            </section>

        </React.Fragment>
    )
}

export default ProductListing;